<template>
    <div class="container">
        <h2 class="page-header"><i18n path="common.install"></i18n></h2>
        <p><i18n path="common.guide.useNpm"></i18n></p>
        <pre class="brush:bash">
            npm i v-selectpage --save
        </pre>
        <p><i18n path="common.guide.include"></i18n></p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vSelectPage from 'v-selectpage';
            Vue.use(vSelectPage, {
                dataLoad: function(vue, data, params){
                    ...
                }
            });
        </pre>

        <div class="panel panel-default">
            <div class="panel-body">
                <i18n path="selectpage.config"></i18n>
                <ul>
                    <li>title</li>
                    <li>language</li>
                    <li>placeholder</li>
                    <li>pageSize</li>
                    <li>dataLoad</li>
                </ul>
            </div>
        </div>


        <h2 class="page-header"><i18n path="common.deploy"></i18n></h2>
        <p>template</p>
        <script type="syntaxhighlighter" class="brush:html">
        <![CDATA[
            <template>
                <v-selectpage :data="list" key-field="id" show-field="name" class="form-control">
                </v-selectpage>
            </template>
        ]]>
        </script>

        <p>script</p>
        <pre class="brush:js">
        export default {
            data(){
                return {
                    list: [
                        {id:1 ,name:'Chicago Bulls',desc:'芝加哥公牛'},
                        {id:2 ,name:'Cleveland Cavaliers',desc:'克里夫兰骑士'},
                        {...}
                    ]
                }
            }
        };
        </pre>

        <h2 class="page-header"><i18n path="common.example"></i18n> <small><i18n path="selectpage.demo.desc"></i18n></small></h2>

        <h4><i18n path="selectpage.demo.single"></i18n></h4>
        <p><i18n path="common.selectedResults"></i18n></p>
        <pre style="border: 0;">{{JSON.stringify(singleValue, null, 4)}}</pre>
        <p>
            <v-selectpage class="form-control" :data="sample" sort="name desc" @values="singleValues"></v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-selectpage class="form-control" :data="sample" sort="name desc" @values="singleValues">
            </v-selectpage>
            ]]>
            </script>
        </showcode>
        <br>


        <h4><i18n path="selectpage.demo.custom"></i18n></h4>
        <p>
            <button type="button" class="btn btn-danger" @click="$refs.selectpage2.$emit('disabled',true);"><i18n path="selectpage.demo.btnDisabled"></i18n></button>
            <button type="button" class="btn btn-default" @click="$refs.selectpage2.$emit('disabled',false);"><i18n path="selectpage.demo.btnEnabled"></i18n></button>
        </p>
        <p>
            <v-selectpage class="form-control" :data="sample" :show-field="renderRow" ref="selectpage2">
            </v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <!-- control buttons -->
            <button type="button" @click="$refs.selectpage2.$emit('disabled',true);">Disabled</button>
            <button type="button" @click="$refs.selectpage2.$emit('disabled',false);">Enabled</button>

            <v-selectpage class="form-control" :data="sample" :show-field="renderRow" ref="selectpage2">
            </v-selectpage>

            < script >
            export default {
                data(){
                    return {
                        renderRow(row){
                            let result = '';
                            if(row && Object.keys(row).length)
                                result = `[<b>${row.id<10?'0'+row.id:row.id}</b>] - ${row.name} (${row.desc})`;
                            return result;
                        }
                    };
                }
            };
            < /script >
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="selectpage.demo.nopage"></i18n></h4>
        <p>
            <v-selectpage class="form-control" :data="sample" :pagination="false" ></v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-selectpage class="form-control" :data="sample" :pagination="false" >
            </v-selectpage>
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="selectpage.demo.modify"></i18n></h4>
        <p>
            <button type="button" class="btn btn-default" @click="selected='22'">
                <i18n path="selectpage.demo.btnModify"></i18n>
            </button>
        </p>
        <p>
            <v-selectpage class="form-control" :data="sample" :selected="selected" ></v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <button type="button" @click="selected='22'">Modify</button>

            <!-- init by {selected: '7'} -->
            <v-selectpage class="form-control" :data="sample" :selected="selected" ></v-selectpage>
            ]]>
            </script>
        </showcode>
        <br>





        <h4>
            <i18n path="selectpage.demo.multiple"></i18n>
            <small>
                <a href="https://codepen.io/terry05/pen/wXNKOK" target="_blank">
                    <i18n path="common.codePen"></i18n>
                </a>
            </small>
        </h4>
        <p><i18n path="common.selectedResults"></i18n></p>
        <pre style="border: 0;">{{JSON.stringify(multipleValue, null, 4)}}</pre>
        <p>
            <button type="button" class="btn btn-default" @click="tmpData=sample1.concat()">
                <i18n path="selectpage.demo.btnChange"></i18n>
            </button>
        </p>

        <p>
            <v-selectpage class="form-control"
                          :data="tmpData"
                          :multiple="true"
                          :max-select-limit="3"
                          selected="3,5,7"
                          @values="multipleValues" >
            </v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <button type="button" @click="tmpData=sample1.concat()">Change data source</button>

            <v-selectpage class="form-control"
                          :data="tmpData"
                          :multiple="true"
                          :max-select-limit="3"
                          selected="3,5,7"
                          @values="multipleValues" >
            ]]>
            </script>
        </showcode>
        <br>




        <h4><i18n path="selectpage.demo.tbSingle"></i18n></h4>
        <p>
            <v-selectpage class="form-control" :data="sample1" :tb-columns="showFields">
            </v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-selectpage class="form-control" :data="sample1" :tb-columns="showFields">
            </v-selectpage>

            < script >
            export default {
                data(){
                    return {
                        sample1: [
                            {id:1 ,name:'The People\'s Republic of China',desc:'中华人民共和国'},
                            {id:2 ,name:'United States of America',desc:'美利坚合众国'},
                            {...}
                        ],
                        showFields:[
                            {title: 'id',data: 'id'},
                            {title: 'name',data: 'name'},
                            {title: 'desc',data: 'desc'},
                        ]
                    }
                }
            };
            < /script >
            ]]>
            </script>
        </showcode>
        <br>



        <h4><i18n path="selectpage.demo.tbMultiple"></i18n></h4>
        <p>
            <v-selectpage class="form-control" :data="sample1" :multiple="true" :tb-columns="showFields">
            </v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <v-selectpage class="form-control" :data="sample1" :multiple="true" :tb-columns="showFields">
            </v-selectpage>
            ]]>
            </script>
        </showcode>
        <br>



        <h4><i18n path="selectpage.demo.server"></i18n></h4>
        <p>
            <i18n path="selectpage.demo.serverResponse"></i18n>:
            <a href="http://www.mocky.io/v2/5b3777a72e0000570036c7a5" target="_blank">Server Response</a>
        </p>
        <p>
            <v-selectpage class="form-control"
                          data="www.mocky.io/v2/5b3777a72e0000570036c7a5"
                          key-field="id"
                          show-field="name"
                          search-field="userName"
                          selected="1"
                          :result-format="resultFormat">
            </v-selectpage>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-selectpage class="form-control"
                          data="http://yourserver/load"
                          key-field="id"
                          show-field="name"
                          search-field="userName"
                          selected="1"
                          :result-format="resultFormat">
            </v-selectpage>

            < script >
            export default {
                data(){
                    return {
                        sample1: [
                            {id:1 ,name:'The People\'s Republic of China',desc:'中华人民共和国'},
                            {id:2 ,name:'United States of America',desc:'美利坚合众国'},
                            {...}
                        ],
                        showFields:[
                            {title: 'id',data: 'id'},
                            {title: 'name',data: 'name'},
                            {title: 'desc',data: 'desc'},
                        ]
                    }
                },
                methods: {
                    resultFormat(resp){
                        if(resp && resp.data) return resp.data.values.gridResult;
                    }
                }
            };
            < /script >
            ]]>
            </script>
        </showcode>
        <br>




        <h2 class="page-header"><i18n path="common.options"></i18n></h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th><i18n path="common.tbTitle.name"></i18n></th>
                    <th><i18n path="common.tbTitle.type"></i18n></th>
                    <th><i18n path="common.tbTitle.desc"></i18n></th>
                    <th><i18n path="common.tbTitle.required"></i18n></th>
                    <th><i18n path="common.tbTitle.default"></i18n></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>data</td>
                    <td>Array|String</td>
                    <td><i18n path="selectpage.option.data"></i18n></td>
                    <td>true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>key-field</td>
                    <td>String</td>
                    <td><i18n path="selectpage.option.keyField"></i18n></td>
                    <td>false</td>
                    <td>id</td>
                </tr>
                <tr>
                    <td>show-field</td>
                    <td>String|Function</td>
                    <td><i18n path="selectpage.option.showField"></i18n></td>
                    <td>false</td>
                    <td>name</td>
                </tr>
                <tr>
                    <td>tb-columns</td>
                    <td>Array</td>
                    <td>
                        <i18n path="selectpage.option.tbColumns"></i18n>
                        <pre class="brush:js;gutter: false;">
                        [
                            { title: 'id',data: 'id' },
                            { title: 'name',data: 'name' },
                            { title: 'desc',data: function(row){
                                return row.name + ' ' +row.desc;
                            }},
                        ]
                        </pre>
                    </td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>sort</td>
                    <td>String</td>
                    <td><i18n path="selectpage.option.sort"></i18n></td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>search-field</td>
                    <td>String</td>
                    <td><i18n path="selectpage.option.searchField"></i18n></td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>params</td>
                    <td>Object</td>
                    <td><i18n path="selectpage.option.params"></i18n></td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>result-format</td>
                    <td>Function</td>
                    <td>
                        <i18n path="selectpage.option.resultFormat"></i18n>
                        <pre class="brush:js;gutter: false;">
                        {
                            totalRow: 100,
                            list:[
                                {...},
                                {...},
                                {...}
                            ]
                        }
                        </pre>
                    </td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>selected</td>
                    <td>String</td>
                    <td><i18n path="selectpage.option.selected"></i18n></td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>title</td>
                    <td>String</td>
                    <td><i18n path="selectpage.option.title"></i18n></td>
                    <td>false</td>
                    <td>SelectPage</td>
                </tr>
                <tr>
                    <td>placeholder</td>
                    <td>String</td>
                    <td><i18n path="selectpage.option.placeholder"></i18n></td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>multiple</td>
                    <td>Boolean</td>
                    <td><i18n path="selectpage.option.multiple"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>language</td>
                    <td>String</td>
                    <td><i18n path="common.lang.desc"></i18n><br>
                        <ul>
                            <li><code>cn</code> <i18n path="common.lang.cn"></i18n></li>
                            <li><code>en</code> <i18n path="common.lang.en"></i18n></li>
                            <li><code>jp</code> <i18n path="common.lang.jp"></i18n></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>cn</td>
                </tr>
                <tr>
                    <td>page-size</td>
                    <td>Number</td>
                    <td><i18n path="page.event.pageSize"></i18n></td>
                    <td>false</td>
                    <td>10</td>
                </tr>
                <tr>
                    <td>max-select-limit</td>
                    <td>Number</td>
                    <td><i18n path="selectpage.option.maxSelectLimit"></i18n></td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>pagination</td>
                    <td>Boolean</td>
                    <td><i18n path="selectpage.option.pagination"></i18n></td>
                    <td>false</td>
                    <td>true</td>
                </tr>
            </tbody>
        </table>

        <h2 class="page-header"><i18n path="common.events"></i18n></h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.param"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
                <th><i18n path="common.tbTitle.args"></i18n></th>
            </tr>
            </thead>
            <tbody>
                <tr>
                    <td>values</td>
                    <td><i18n path="selectpage.event.values"></i18n></td>
                    <td>Array</td>
                </tr>
                <tr>
                    <td>removed</td>
                    <td><i18n path="selectpage.event.removed"></i18n></td>
                    <td>Array</td>
                </tr>
            </tbody>
        </table>



        <h2 class="page-header">API</h2>
        <p><i18n path="selectpage.api.desc"></i18n></p>
        <script type="syntaxhighlighter" class="brush:js;gutter: false;">
        <![CDATA[
            <v-selectpage :data="data" ref="sp" >

            //call api
            this.$refs.sp.$emit('disabled', true);
        ]]>
        </script>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.param"></i18n></th>
                <th><i18n path="common.tbTitle.type"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>disabled</td>
                <td>Boolean</td>
                <td><i18n path="selectpage.api.disabled"></i18n></td>
            </tr>
            <tr>
                <td>clear</td>
                <td></td>
                <td><i18n path="selectpage.api.clear"></i18n></td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "Demo",
        data(){
            return {
                btnText: 'please select',
                selectResult: {},


                singleValue: null,
                multipleValue: null,
                selected: '7',
                renderRow(row){
                    let result = '';
                    if(row && Object.keys(row).length)
                        result = `[<b>${row.id<10?'0'+row.id:row.id}</b>] - ${row.name} (${row.desc})`;
                    return result;
                },
                showFields:[
                    {title: 'id',data: 'id'},
                    {title: 'name',data: 'name'},
                    {title: 'desc',data: 'desc'},
                ],
                tmpData: [],
                sample: [
                    {id:1 ,name:'Chicago Bulls',desc:'芝加哥公牛'},
                    {id:2 ,name:'Cleveland Cavaliers',desc:'克里夫兰骑士'},
                    {id:3 ,name:'Detroit Pistons',desc:'底特律活塞'},
                    {id:4 ,name:'Indiana Pacers',desc:'印第安纳步行者'},
                    {id:5 ,name:'Milwaukee Bucks',desc:'密尔沃基雄鹿'},
                    {id:6 ,name:'Brooklyn Nets',desc:'布鲁克林篮网'},
                    {id:7 ,name:'Boston Celtics',desc:'波士顿凯尔特人'},
                    {id:8 ,name:'New York Knicks',desc:'纽约尼克斯'},
                    {id:9 ,name:'Philadelphia 76ers',desc:'费城76人'},
                    {id:10,name:'Toronto Raptors',desc:'多伦多猛龙'},
                    {id:11,name:'Atlanta Hawks',desc:'亚特兰大老鹰'},
                    {id:12,name:'Charlotte Hornets',desc:'夏洛特黄蜂'},
                    {id:13,name:'Miami Heat',desc:'迈阿密热火'},
                    {id:14,name:'Orlando Magic',desc:'奥兰多魔术'},
                    {id:15,name:'Washington Wizards',desc:'华盛顿奇才'},
                    {id:16,name:'Denver Nuggets',desc:'丹佛掘金'},
                    {id:17,name:'Minnesota Timberwolves',desc:'明尼苏达森林狼'},
                    {id:18,name:'Oklahoma City Thunder',desc:'俄克拉荷马雷霆'},
                    {id:19,name:'Portland Trail Blazers',desc:'波特兰开拓者'},
                    {id:20,name:'Utah Jazz',desc:'犹他爵士'},
                    {id:21,name:'Golden State Warriors',desc:'金州勇士'},
                    {id:22,name:'Los Angeles Clippers',desc:'洛杉矶快船'},
                    {id:23,name:'Los Angeles Lakers',desc:'洛杉矶湖人'},
                    {id:24,name:'Phoenix Suns',desc:'菲尼克斯太阳'},
                    {id:25,name:'Sacramento Kings',desc:'萨克拉门托国王'},
                    {id:26,name:'Dallas Mavericks',desc:'达拉斯小牛'},
                    {id:27,name:'Houston Rockets',desc:'休斯顿火箭'},
                    {id:28,name:'Memphis Grizzlies',desc:'孟菲斯灰熊'},
                    {id:29,name:'New Orleans Pelicans',desc:'新奥尔良鹈鹕'},
                    {id:30,name:'San Antonio Spurs',desc:'圣安东尼奥马刺'}
                ],
                sample1: [
                    {id:1 ,name:'The People\'s Republic of China',desc:'中华人民共和国'},
                    {id:2 ,name:'United States of America',desc:'美利坚合众国'},
                    {id:3 ,name:'United Kingdom of Great Britain and Northern Ireland',desc:'大不列颠及北爱尔兰联合王国'},
                    {id:4 ,name:'Japan',desc:'日本'},
                    {id:5 ,name:'Russian Federation',desc:'俄罗斯联邦'},
                    {id:6 ,name:'The Federal Republic of Germany',desc:'德意志联邦共和国'},
                    {id:7 ,name:'French Republic',desc:'法兰西共和国'},
                    {id:8 ,name:'The Kingdom of Spain',desc:'西班牙王国'},
                    {id:9 ,name:'Republic of India',desc:'印度共和国'},
                    {id:10,name:'Repubblica Italiana',desc:'意大利共和国'},
                    {id:11,name:'Canada',desc:'加拿大'},
                    {id:12,name:'The Republic of Argentina',desc:'阿根廷共和国'},
                    {id:13,name:'The Kingdom of Sweden',desc:'瑞典王国'},
                    {id:14,name:'The Kingdom of Norway',desc:'挪威王国'},
                    {id:15,name:'The Czech Republic',desc:'捷克共和国'}
                ]
            };
        },
        methods: {
            regionChange(d){
                console.log(d);
                this.btnText = (!d.province&&!d.city&&!d.area&&!d.town)?'请选择':
                    `${this.getValue(d.province)}${this.getValue(d.city)}${this.getValue(d.area)}${this.getValue(d.town)}`.trim();
            },
            getValue(obj){
                return obj?obj.value:'';
            },
            selectLogs(data){
                if(data) this.selectResult = data;
            },
            singleValues(val){
                this.singleValue = val;
            },
            multipleValues(val){
                this.multipleValue = val;
            },
            resultFormat(resp){
                if(resp && resp.data){
                    let query = JSON.parse(resp.config.data);
                    if(Object.keys(query).findIndex(val=>val === 'searchKey') !== -1){
                        let list = resp.data.values.gridResult.list;
                        let row = list.find(val=>String(val[query.searchKey]) === query.searchValue);
                        return {
                            totalRow: resp.data.values.totalRow,
                            list: [row]
                        };
                    }else return resp.data.values.gridResult;
                }
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
            this.tmpData = this.sample;
        }
    }
</script>

<style lang="scss" scoped>
.info-box{
    background-color: #F5F5F5;
    border: 0;
    border-radius: 4px;
    padding: 10px;
}
</style>


